<template>
  <div>
    <div class="kc_header_wrap">
      <div class="kc_header">
        <div></div>
        <div>特色课</div>
        <img @click="search"
          src="data:image/png;base64,"
        />
      </div>
    </div>

    <div class="kc_vant">
      <van-dropdown-menu style="width:100%;height:1rem">
        <van-dropdown-item title="分类" ref="item">
          <div v-for="(item,index) in list" :key="index" style="border-bottom:1px solid lightgray">
            <div class="kc_item_name">{{item.name}}</div>
            <div class="kc_item_child">
              <div v-for="(v,i) in item.child" :key="i" class="kc_item_child_v">{{v.name}}</div>
            </div>
          </div>
          <div class="kc_item_child_button">
            <button style="border:1px solid lightgray">重置</button>
            <button style="background:#EB6100;color:white">确定</button>
          </div>
        </van-dropdown-item>

        <van-dropdown-item title="排序" ref="item">
          <div v-for="(item,index) in sortList" :key="index" class="kc_sort">{{item}}</div>
        </van-dropdown-item>

        <van-dropdown-item title="筛选" ref="item">
          <div class="kc_item_child" style="height:5rem">
            <div
              v-for="(v,i) in this.course_types"
              :key="i"
              class="kc_item_child_v"
              style="margin:0.2rem 0.2rem"
            >{{v.value}}</div>
          </div>
        </van-dropdown-item>
      </van-dropdown-menu>
    </div>

    <div class="ul" style="margin-bottom:2rem">
      <div class="li" v-for="(v,i) of this.item" :key="i">
        <h4>
          <span>{{ v.title }}</span>
        </h4>
        <div style="font-size:0.3rem;color:gray">
          {{v.start_play_date | time}}
          <span style="margin:0rem 0.1rem">~</span>
          {{v.end_play_date | time}}
          <span style="margin:0rem 0.1rem">|</span>
          共{{v.total_periods}}课时
        </div>
        <div class="bo" v-for="(item,index) in v.teachers_list" :key="index">
          <h3>
            <img :src="item.teacher_avatar" alt />
            <p>{{ item.teacher_name }}</p>
          </h3>
        </div>
        <h2>
          <p>{{ v.sales_num }}人报名</p>
          <b id="pr">{{ v.price | price}}</b>
        </h2>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      sortList: ["综合排序", "最新", "最热", "价格从低到高", "价格从高到低"],
      course_types: [
        { type: 0, value: "全部" },
        { type: 2, value: "大班课" },
        { type: 3, value: "小班课" },
        { type: 4, value: "公开课" },
        { type: 5, value: "点播课" },
        { type: 7, value: "面授课" },
        { type: 8, value: "音频课" },
        { type: 9, value: "系统课" },
        { type: 10, value: "图文课" },
        { type: 11, value: "会员课" }
      ],
      item: []
    };
  },
  mounted() {
    this.http.get("/api/app/courseClassify").then(resp => {
      window.console.log(resp);
      this.list = resp.data.data.attrclassify;
    });

    this.http
      .get(
        "/api/app/courseBasis?page=1&limit=10&course_type=3&classify_id=&order_by=&attr_val_id=&is_vip=0"
      )
      .then(resp => {
        window.console.log(resp);
        this.item = resp.data.data.list;
      });
  },
  filters:{
    time(value){
      let shijian = new Date(value).toLocaleString()
      return shijian
    },
    price(value){
      if(value == 0){
        return "免费"
      }else{
        return `￥${(value/100).toFixed(2)}`
      }
    }
  },
  methods:{
    search(){
      this.$router.push('/Search')
    }
  }
};
</script>

<style   lang="less">
.kc_header_wrap {
  width: 100%;
  height: 100px;
}
.kc_header {
  position: fixed;
  top: 0rem;
  width: 90%;
  height: 99px;
  border-bottom: 1px solid lightgray;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.5rem;
  padding: 0rem 5%;
  background: white;
  z-index: 999;
}
.kc_header img {
  height: 40%;
}
.kc_vant {
  width: 100%;
  height: 1rem;
}
.kc_item_name {
  padding: 0.3rem;
  font-size: 0.3rem;
}
.kc_item_child {
  width: 94%;
  height: 3rem;
  padding: 0rem 3%;
  display: flex;
  flex-wrap: wrap;
  background: white;
}
.kc_item_child_v {
  width: 20%;
  height: 1rem;
  background: #f5f5f5;
  font-size: 0.3rem;
  margin-right: 0.45rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
}
.kc_item_child_button {
  width: 100%;
  height: 2rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.kc_item_child_button button {
  width: 45%;
  height: 0.8rem;
  font-size: 0.4rem;
  border: none;
  outline: none;
  background: white;
  border-radius: 10px;
}
.kc_sort {
  width: 100%;
  height: 1.5rem;
  font-size: 0.35rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid lightgray;
}
.ul {
    width: 96%;
    margin: 0.4rem auto;
        .li {
          width: 100%;
          height: 407px;
          background: #fff;
          border-radius: 10px;
          margin-bottom: 30px;
          padding: 0px 29px 0 29px;
          box-sizing: border-box;
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          h4 {
            font-size: 33px;
            display: flex;
            justify-content: space-between;
          }
          .bo {
            display: flex;
            flex-wrap: wrap;
            h3 {
              display: flex;
              height: 55px;
              align-items: center;
              margin-bottom: 15px;
              img {
                width: 55px;
                height: 55px;
                border-radius: 50%;
              }
              p {
                font-size: 20px;
                color: #666;
                margin: 0 20px;
              }
            }
          }
          h2 {
            display: flex;
            align-items: center;
            justify-content: space-between;
            border-top: 1px solid #eee;
            padding-top: 26px;
            p {
              font-size: 23px;
              color: #666;
            }
            b {
              font-size: 33px;
              color: #389d1d;
            }
          }
        }
      }
</style>